<template>
  <div class="container py-5">
    <NavTabs />
    <Spinner v-if="isLoading" />
    <template v-else>
      <h1 class="mt-5">熱門餐廳</h1>

      <hr />
      <div
        v-for="restaurant in topRestaurants"
        :key="restaurant.id"
        class="row m-2"
        style="justify-content: center;"
      >
        <div class="card text-left col-md-8 mb-3">
          <div class="card-body">
            <div class="float-left" style="width:130px;height:130px;">
              <img
                class="card-img"
                :src="restaurant.image"
                style="width:150px;height:100px;margin:-1.25rem 0 0 -2.25rem;"
                alt
              />
            </div>
            <h4 class="card-title">{{restaurant.name}}</h4>
            <span class="badge badge-secondary">收藏數：{{restaurant.FavoriteCount}}</span>
            <p class="card-text" style="font-weight: 600;">{{restaurant.description}}</p>
            <div class="row">
              <router-link
                :to="{name:'restaurant',params:{id:restaurant.id}}"
                class="btn btn-primary mr-3"
              >show</router-link>

              <button
                v-if="restaurant.isFavorited"
                @click.stop.prevent="deleteFavorite(restaurant.id)"
                class="btn btn-danger"
              >移除最愛</button>

              <button
                v-else
                @click.stop.prevent="addFavorite(restaurant.id)"
                class="btn btn-primary"
              >加到最愛</button>
            </div>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import NavTabs from "../components/NavTabs";
import restaurantsAPI from "../apis/restaurants";
import usersAPI from "../apis/users";
import { Toast } from "../utils/helpers";
import Spinner from "../components/Spinner";

export default {
  components: { NavTabs, Spinner },
  data() {
    return {
      topRestaurants: [],
      isLoading: true
    };
  },
  methods: {
    async fetchRestaurants() {
      try {
        const { data } = await restaurantsAPI.getTopRestaurants();
        this.topRestaurants = data.restaurants;
        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        Toast.fire({
          icon: "error",
          title: "無法取得餐廳資料，請稍後再試"
        });
      }
    },
    async addFavorite(restaurantId) {
      try {
        const { data } = await usersAPI.addFavorite({ restaurantId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }

        this.topRestaurants.map(restaurant => {
          if (restaurant.id === restaurantId) {
            restaurant.isFavorited = true;
            restaurant.FavoriteCount++;
          }
        });
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法將餐廳加入最愛，請稍後再試"
        });
      }
    },
    async deleteFavorite(restaurantId) {
      try {
        const { data } = await usersAPI.deleteFavorite({ restaurantId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }

        this.topRestaurants.map(restaurant => {
          if (restaurant.id === restaurantId) {
            restaurant.isFavorited = false;
            restaurant.FavoriteCount--;
          }
        });
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法將餐廳移除最愛，請稍後再試"
        });
      }
    }
  },
  created() {
    this.fetchRestaurants();
  }
};
</script>
